<script lang="ts">
    import {
        Dialog,
        Card,
        CardTitle,
        MaterialApp,
        List,
        ListItem,
    } from "svelte-materialify";

    import type { IStarknetWindowObject, WalletProvider } from "../types";
    import Bowser from "bowser";

    export let callback = () => {};
    export let installed: IStarknetWindowObject[] = [];
    export let discovery: WalletProvider[] = [];

    const title = "Connect a wallet";

    const browserName = Bowser.getParser(window.navigator.userAgent)
        .getBrowser()
        .name?.toLowerCase();
    let browser;
    switch (browserName) {
        case "chrome":
        case "chromium":
        case "electron":
        case "microsoft edge":
            browser = "chrome";
            break;
        case "firefox":
            browser = "firefox";
            break;
    }

    const unifiedWallets = browser
        ? [
              // include installed 1st
              ...installed,
              // discovery 2nd, and remove wallets with no support
              // for the current browser
              ...discovery.filter(dw => !!dw.downloads[browser]),
          ]
        : installed;

    console.log("modal", { unifiedWallets, browser, installed, discovery });

    let active = true;
</script>

{#if active}
    <MaterialApp>
        <Dialog bind:active>
            <Card style="min-width: 30vw">
                <CardTitle
                    style="
                    display: flex;
                    justify-content: space-between;
                    align-items: center;
                    ">
                    {title}
                    <img
                        alt="Close"
                        style="cursor: pointer;"
                        on:click={() => (active = false)}
                        src="data:image/svg+xml;base64," />
                </CardTitle>

                {#if unifiedWallets.length}
                    <List style="margin-inline-start: 24px; margin-inline-end: 24px">
                        {#each unifiedWallets as wallet, idx}
                            <!--suppress CssUnresolvedCustomProperty -->
                            <ListItem
                                style="
                                border: 1px solid lightgrey;
                                /* use same border-radius as dialog's */
                                border-radius: var(--mdc-shape-medium, 4px);
                                height: 58px;
                                background: #edeef2;
                                margin-bottom: 10px;
                                "
                                on:click={() => {
                                    if (idx < installed.length) {
                                        callback(wallet);
                                    } else {
                                        window.open(wallet.downloads[browser], "_blank");
                                        callback(undefined);
                                    }
                                    active = false;
                                }}>
                                {idx >= installed.length ? "Install " : ""}{wallet.name ||
                                    "Injected"}

                                <span slot="append">
                                    {#if !!wallet.icon}
                                        <img
                                            width="32px"
                                            src={wallet.icon}
                                            alt="{wallet.name} logo" />
                                    {:else}
                                        <img
                                            alt="{wallet.name || 'Injected'} logo"
                                            src="data:image/svg+xml;base64," />
                                    {/if}
                                </span>
                            </ListItem>
                        {/each}
                    </List>
                {:else}
                    <div class="mdc-typography--headline6">
                        No compatible wallets found for {browserName}
                    </div>
                {/if}
            </Card>
        </Dialog>
    </MaterialApp>
{/if}
